import React from 'react';
import { format } from 'date-fns';

/**
 *    
 * @param {Object} props -  
 * @param {Object} props.dateRange -    {startDate, endDate}
 * @param {Function} props.onStartDateChange -    
 * @param {Function} props.onEndDateChange -    
 * @param {Function} props.onReset -   
 * @param {number} props.totalCount -   
 * @param {number} props.filteredCount -   
 * @returns {JSX.Element} -    
 */
const DateRangeFilter = ({ 
  dateRange, 
  onStartDateChange, 
  onEndDateChange, 
  onReset,
  totalCount,
  filteredCount
}) => {
  //    value    
  const startDateStr = dateRange.startDate 
    ? format(dateRange.startDate, 'yyyy-MM-dd') 
    : '';
    
  const endDateStr = dateRange.endDate 
    ? format(dateRange.endDate, 'yyyy-MM-dd') 
    : '';

  //   
  const isFilterActive = dateRange.startDate || dateRange.endDate;
  
  //    
  const getFilterMessage = () => {
    if (!isFilterActive) return null;
    
    //   
    if (filteredCount === totalCount) {
      return <span className="filter-message">   .</span>;
    }
    
    return (
      <span className="filter-message">
         {totalCount}  {filteredCount}   .
      </span>
    );
  };

  return (
    <div className="date-range-filter">
      <div className="filter-header">
        <h4>  </h4>
        {isFilterActive && (
          <button 
            className="reset-button" 
            onClick={onReset}
          >
            
          </button>
        )}
      </div>
      
      <div className="filter-inputs">
        <div className="date-input-group">
          <label htmlFor="start-date"> :</label>
          <input
            id="start-date"
            type="date"
            value={startDateStr}
            onChange={onStartDateChange}
          />
        </div>
        
        <span className="date-separator">~</span>
        
        <div className="date-input-group">
          <label htmlFor="end-date"> :</label>
          <input
            id="end-date"
            type="date"
            value={endDateStr}
            onChange={onEndDateChange}
          />
        </div>
      </div>
      
      {getFilterMessage()}
    </div>
  );
};

export default DateRangeFilter;
