//   
export const DEFAULT_OPTIONS = {
  equipment: {
    showImages: true,
    showGrade: true,
    showQuality: true,
    showRefine: true,        // 
    showHigherRefine: true,  //  
    showTranscendence: true, // 
    showElixir: true         // 
  },
  accessory: {
    showImages: true,
    showGrade: true,
    showQuality: true
  },
  abilityStone: {
    show: true  //    
  }
};

//    
export const loadOptions = () => {
  try {
    const savedOptions = localStorage.getItem('speclog_options');
    if (savedOptions) {
      return JSON.parse(savedOptions);
    }
    //     
    return DEFAULT_OPTIONS;
  } catch (error) {
    console.error('  :', error);
    return DEFAULT_OPTIONS;
  }
};

//    
export const saveOptions = (options) => {
  try {
    localStorage.setItem('speclog_options', JSON.stringify(options));
  } catch (error) {
    console.error('  :', error);
  }
};
